#datetime-picker {
  border: 3px solid #28a745;
  border-radius: 5px;
  height: 39px;
  width: 306px;
  text-align: center;
  margin-right: 5px;
  margin-bottom: 5px;
}

.timer {
  display: flex;
  font-size: 17px;
  padding-top: 13px;
}
.field {
  position: relative;
  min-width: 60px;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 15px;
  text-align: center;
}
.field::before {
  content: attr(data-title);
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  font-size: 14px;
}
.field:not(:last-child)::after {
  content: ':';
  position: absolute;
  right: -15px;
}
